.google-maps-container {
  width: 100%;
  height: 100%;
  min-height: 400px;
}

:host {
  display: block;
  width: 100%;
  height: 100%;
}

.google-maps-zone-container {
  display: flex;
  flex-direction: column;
  width: 100%;

  .map-container {
    width: 100%;
    border: 1px solid #e0e0e0;
    border-radius: 4px;
    overflow: hidden;
  }

  .error-message {
    margin-top: 8px;
    margin-bottom: 8px;
  }

  .actions {
    margin-top: 16px;
    display: flex;

    button {
      transition: background-color 0.2s;

      &:not(:last-child) {
        margin-right: 8px;
      }
    }
  }
}
